
import UploaderItem from './uploader-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
props: {
images: {
type: Array,
default: () => []
},
imagesEx: {
type: Array,
default: () => []
},
max: {
type: Number,
default: 1
},
showHeader: {
type: Boolean,
default: true
},
boxWidth: {
type: String,
default: '80px'
},
boxHeight: {
type: String,
default: '80px'
},
showTip: {
type: Boolean,
default: true
},
readonly: {
type: Boolean,
default: false
},
title: {
type: String,
default: '图片上传'
},

handleClick: {
type: Boolean,
default: false
},

autoUpload: {
type: Boolean,
default: true
},
uploadUrl: {
type: String,
default: ''
},
size: {
type: String,
default: 'normal'
},
capture: {
type: String,
default: ''
},
name: {
type: String,
default: 'img'
},
params: {
type: Object,
default: null
},
headers: {
type: Object,
default: null
}
},
components: {
UploaderItem, Flexbox, FlexboxItem, Grid, GridItem
},
data () {
return {
addImg: '../../static/img/image_icon_add_normal.png',
addFlag: false
}
},
methods: {
add () {
if (this.handleClick) {
this.$emit('add-image')
}
},

remove () {

document.querySelector('.weui-uploader__input').value = ''
if (this.handleClick) {
this.$emit('remove-image')
} else {
this.images.shift()
}
},
preview (image) {

this.$emit('preview', image)
},
addflag () {
this.addFlag = true
},

change (name, index) {
if (this.handleClick) {
return
}
let file = this.$refs[name].files[0]
let formData = new window.FormData()
formData.append(this.name, file)
if (this.params) {
for (let key in this.params) {
formData.append(key, this.params[key])
}
}

if (this.autoUpload) {
if (!this.uploadUrl) {
console.error('uploadUrl不应为空')
}

if (this.$vux && this.$vux.loading) {
this.$vux.loading.show('正在上传')
}
let objk = {name: file.name, url: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=1510446447,531093518&fm=173&app=49&f=JPEG'}
if (index) {
this.$set(this.images, index, objk)
this.$emit('onsuccess', this.images)
} else if (name === 'inputGo') {
this.imagesEx.push(objk)
console.log(this.imagesEx)
}









} else {
this.$emit('upload-image', formData)
}
}
},
computed: {
showCapture () {
return this.capture || undefined
}
}
}



